*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Helvetica Neue",Helvetica,Arial;
}
body{
  background:#F8F6F5;
  color:#fff;
}

button{
  background:#fff;
  border:0;
  font-size: 18px;
  padding:15px;
  cursor: pointer;
}

.operators{
  display: flex;
}

.operators button{
  flex: 1 1 0%;
  background:#66A3A3;
  font-weight: 700;
  color:#fff;
}

.digits{
  display: flex;
  flex-wrap: wrap;
}

.digits button{
  flex: 1 1 33.333%;
  max-width: 33.333%;
}

.App{
  display:flex;
  justify-content:center;
  align-items:center;
  width: 390px;
  margin: auto;
  height: 350px;
}

.calculator{
 margin-top: 60px;
}

.display{
  padding:20px;
  text-align:right;
  background-color:#003333;
  font-size:20px;
  font-weight: 500;
}

.display p{
  font-size: 15px;
  display:inline-block;
}
